import {
    breakpoint_m_maxval,
    breakpoint_s_maxval,
    css,
    cssSelector,
    cssVariableAsVar,
    GenerateStyles,
    style,
    unsafeCSS,
} from '@umetrics/sartorius-ui-base';

export const headerHeight = unsafeCSS('48px');

export const headerStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    return [
        style,
        css`
            :host {
                width: 100%;
            }

            ${root} {
                display: flex;
                justify-content: space-between;
                height: 48px;
                width: 100%;
                position: relative;
                z-index: 10;
            }

            ${child('pane')} {
                flex: 1;
                display: flex;
            }

            ${child('pane-left')} {
                flex-direction: row;
                width: 100%;
                justify-content: left;
                align-items: center;

                gap: 10px;
                margin-left: var(--header-spacing-outer, 12px);
            }
            ${child('pane-center')} {
                justify-content: center;
                flex: 0;
            }
            ${child('pane-right')} {
                justify-content: right;
                align-items: center;
                margin-right: var(--header-spacing-outer, 12px);
            }

            ${child('logo')} {
                height: 48px;
            }

            ::slotted([slot='menu-slot']) {
                margin-right: 4px;
                width: ${headerHeight};
            }
            ::slotted([slot='product-slot']) {
                font-size: 1.2rem;
            }
            ::slotted([slot='misc-slot']) {
                width: ${headerHeight};
            }
            ::slotted([slot='user-slot']) {
                font-size: 1rem;
                margin-left: 4px;
            }
            ::slotted([slot='logout-slot']) {
                margin-left: 4px;
                width: ${headerHeight};
            }

            ${child('logo-link')} {
                display: flex;
                align-items: center;
            }
            ${child('logo-link')}:hover {
                cursor: pointer;
                background-color: transparent;
            }

            ${is('primary')} {
                background-color: ${cssVariableAsVar('color_primary_3')};
                color: ${cssVariableAsVar('color_primary_2')};
            }

            ${is('secondary')} {
                background-color: ${cssVariableAsVar('color_primary_2')};
                color: ${cssVariableAsVar('color_primary_3')};
            }

            ${is('tertiary')} {
                background-color: ${cssVariableAsVar('color_primary_1')};
                color: ${cssVariableAsVar('color_primary_3')};
            }

            ${child('logo-link')}:focus {
                outline: none;
                border: 1px solid ${cssVariableAsVar('color_primary_2')};
                margin: 1px 0 1px -2px;
            }

            @media screen and (max-width: ${breakpoint_m_maxval}px) {
                ::slotted([slot='menu-slot']) {
                    margin-right: 2px;
                }
                ::slotted([slot='product-slot']) {
                    font-size: 1rem;
                }
                ::slotted([slot='user-slot']) {
                    margin-left: 2px;
                    font-size: 0.8rem;
                }
                ::slotted([slot='logout-slot']) {
                    margin-left: 2px;
                }
            }

            @media screen and (max-width: ${breakpoint_s_maxval}px) {
                ::slotted([slot='product-slot']) {
                    font-size: 0.8rem;
                }
                ${child('logo')} {
                    height: 40px;
                }
            }
        `,
    ];
};
