import { BaseElement, createClassName, defineElement, html, ifDefined, property } from '@umetrics/sartorius-ui-base';
import { headerHeight, headerStyles } from './headerStyle';
import { HeaderProperties, HeaderSlot } from './headerProperties';
import { HeaderType } from './headerType';
import { version } from './metadata';
import * as logoSrc from './logo';

@defineElement({ tagName: 'header', styles: headerStyles, version })
export class Header extends BaseElement implements HeaderProperties {
    // Static members
    public static readonly height = headerHeight;

    // Private members

    // Protected members

    // Public members
    @property({ type: String })
    height = '';
    @property({ type: HeaderType })
    type = HeaderType.Primary;
    @property({ type: String })
    logo = '';
    @property({ type: String, attribute: 'logo-alt-text' })
    logoAltText = '';
    @property({ type: String, attribute: 'logo-link' })
    logoLink = '';
    @property({ type: String, attribute: 'logo-target' })
    logoTarget = ''; // For example _blank

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            [this.type]: true,
        });

        const headerStyle = this.height ? `height: ${this.height};` : ``;

        return html` <header class="${rootClasses}" style="${ifDefined(headerStyle)}">
            <div class="${baseClassName}-pane ${baseClassName}-pane-left">
                <slot name="${HeaderSlot.MENU_SLOT}"></slot>
                <slot name="${HeaderSlot.PRODUCT_SLOT}"></slot>
            </div>
            <div class="${baseClassName}-pane ${baseClassName}-pane-center">${this._renderLogo(baseClassName)}</div>
            <div class="${baseClassName}-pane ${baseClassName}-pane-right">
                <slot name="${HeaderSlot.MISC_SLOT}"></slot>
                <slot name="${HeaderSlot.USER_SLOT}"></slot>
                <slot name="${HeaderSlot.LOGOUT_SLOT}"></slot>
            </div>
        </header>`;
    }

    private _renderLogo(baseClassName) {
        const img = html`
            <img
                class="${baseClassName}-logo"
                src="${this.logo ? this.logo : logoSrc.data}"
                alt="${ifDefined(this.logoAltText)}"
            />
        `;
        if (this.logoLink) {
            return html`<a
                class="${baseClassName}-logo-link"
                href="${this.logoLink}"
                target="${this.logoTarget}"
                rel="noopener noreferrer"
                aria-label=${ifDefined(this.logoAltText)}
                >${img}</a
            >`;
        }
        return img;
    }
}
